  border-radius: 0;
  border: 1px solid #9E2B25; }
  .do-login:focus, .do-login:active, .do-login:hover {
    -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.9);
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.9);
    background-color: #2C4251;
    color: white;
    border: 1px solid #2C4251; }

.login-feedback {
  margin: 1rem 0 0 0;
  min-height: 60px; }

.login-feedback.error {
  color: #ff3860; }

.description {
  color: white; }
  .description a {
    color: white;
    border-bottom: 2px solid #9E2B25; }
    .description a:hover {
      border-bottom: 2px solid white; }

.credits {
  font-size: 14px;
  color: silver;
  font-family: monospace; }
  .credits a {
    color: silver;
    border-bottom: 1px solid silver; }
  .credits a:hover {
    color: white;
    border-bottom: 2px solid #9E2B25; }

.info-text {
  font-size: 19px;
  font-weight: 400; }
